import { configureStore } from '@reduxjs/toolkit'
import restaurantReducer from './restaurantSlice'
import kitchenReducer from './kitchenSlice'
import orderItemReducer from './orderItemSlice'
import paymentReducer from './paymentSlice'
import billReducer from './billSlice'
import authReducer from './authSlice'

const rootReducer = {
  baseData: restaurantReducer,
  kitchenData: kitchenReducer,
  orderItemData: orderItemReducer,
  paymentData: paymentReducer,
  billData: billReducer,
  authData: authReducer,
}

const store = configureStore({
  reducer: rootReducer,
  devTools: false,
})

export default store
